<template>
    <div :id="data.sectionId" class="section">
        <div class="min-height">
            <hero-image :suspendAnimations="suspendAnimations" v-if="data.heroImage" :image="data.heroImage" :cut="data.teaser" :class="[getColour('colour1', 'text')]"></hero-image>

            <div class="content-container-overflow">
                <div class="content-container">
                    <div class="bg-pattern" :class="['cut-' + data.teaser, this.getColour('colour1', 'background')]">
                        <div ref="bgPattern" :style="{ backgroundImage: 'url('+ backgroundPatternPath +')' }"></div>
                    </div>
                    <div class="content-inner" :style="{ height: sectionHeight + 'px' }">

                        <big-text :suspendAnimations="suspendAnimations" v-for="(title, index) in data.title"
                                  :key="index"
                                  :slot-id="'bigText' + (parseInt(index)+1)">
                            {{ title }}
                        </big-text>

                        <member :suspendAnimations="suspendAnimations" v-for="(member, index) in data.members"
                                :key="member.uuid"
                                :slot-id="'member' + (parseInt(index)+1)"
                                :jobTitle="member.title"
                                :name="member.firstname + ' ' + member.lastname"
                                :image="member.imageUrl">
                        </member>

                        <paragraph :suspendAnimations="suspendAnimations" slot-id="paragraph1">{{ data.description }}</paragraph>

                        <hiring-banner v-for="(vacancy, index) in data.vacancies" :key="vacancy.uuid" :suspendAnimations="suspendAnimations" :slot-id="'hiringBanner' + (parseInt(index)+1)" :title="vacancy.title" :url="vacancy.url">{{ data.description }}</hiring-banner>

                        <slot></slot>
                    </div>
                </div>
            </div>
        </div>
        <div class="section-bottom" ref="sectionBottom"></div>
    </div>

</template>

<script>
    import { mapGetters } from 'vuex'

    import { TweenMax } from 'gsap';
    import ScrollMagic from 'scrollmagic';
    import 'animation.gsap'
    import 'debug.addIndicators'

    import sectionMixin from '../sectionMixin'

    import colourSchemes from '../store/colourSchemes';
    import colourHelper from '../helpers/colours';

    import SectionTeaser from './SectionTeaser';
    import HeroImage from './HeroImage';

    import HiringBanner from './HiringBanner';
    import Member from './Member'
    import BigText from './BigText';
    import Paragraph from './Paragraph';

    export default {
        name: "employee-section",
        props: ['data'],
        mixins: [sectionMixin],
        provide() {
            return {
                section: this,
            };
        },
        data() {
            return {
                scrollPos: 0,
                sectionHeight: 0,
                colourSchemes: colourSchemes,
            }
        },
        components: {
            BigText,
            HiringBanner,
            Member,
            Paragraph,
            SectionTeaser,
            HeroImage
        },
        computed: {
            backgroundPatternPath() {
                return '/patterns/' + encodeURI(this.data.backgroundPattern);
            }
        },
        methods: {
            getColour(colourName, classname) {
                const colour = this.colourSchemes[this.data.colourScheme][colourName];

                if(classname === 'text') {
                    return colourHelper.textClass(colour);
                } else if(classname === 'background') {
                    return colourHelper.backgroundClass(colour);
                } else {
                    return colour;
                }
            },
            calcSectionHeight() {
                let max = 0;

                for(const child of this.$children) {
                    if('slotId' in child) {
                        let top = parseInt(child.top);
                        if(top + child.$el.offsetHeight > max) {
                            max = top + child.$el.offsetHeight;
                        }
                    }
                }

                this.sectionHeight = max + window.innerHeight*.2;
            },
        },
        mounted() {

            let timeout = null;
            window.addEventListener('resize', () => {
                clearTimeout(timeout);
                timeout = setTimeout(this.calcSectionHeight, 200)
            })
            //this.calcSectionHeight();
            setTimeout(this.calcSectionHeight, 50);



            let isDesktop = window.innerWidth > 1024;
            window.addEventListener('resize', () => {
                isDesktop = window.innerWidth > 1024;
            })
            let suspendAtScrollY = null;
            window.addEventListener('scroll', (e) => {
                if(!isDesktop) return;

                if(!suspendAtScrollY) suspendAtScrollY = window.scrollY;

                // Todo: Switch to transform
                if(this.data.isInView) {
                    const offset = (window.scrollY - suspendAtScrollY) * .4;
                    //console.log('transformY('+ offset +'px)');
                    this.$refs.bgPattern.style.transform = 'translateY('+ offset +'px)';
                    //this.$refs.bgPattern.style.backgroundPositionY = window.scrollY*.4 + 'px';
                }
            });

        }
    }
</script>

<style lang="scss" scoped>

    @import "../scss/section";

</style>
