        methods: {
            goToSection(section) {
                const sectionId = section.id;
                const sectionEl = document.querySelector('#' + sectionId);
                const sectionContainerEl = document.querySelector('.section-container');
                const rect = sectionEl.getBoundingClientRect();
                if(sectionEl) {
                    sectionContainerEl.classList.add('fade-out');
                    setTimeout(() => {
                        window.scrollTo(0, rect.top + window.scrollY);
                        sectionContainerEl.classList.remove('fade-out');
                    }, 300)
                }
            },
            calcBreakpoint() {
                let max = -1;
                for(const point of this.breakpoints) {
                    if(window.innerWidth < point) {
                        max = point;
                    }
                }
                this.$store.commit('setCurrentBreakpoint', max);
            }
        },
        created() {
            this.$store.dispatch('fetchSections');
        },
        mounted() {

            let lastScrollPos = window.pageYOffset;
            document.addEventListener('scroll', (e) => {
                if(window.pageYOffset > lastScrollPos) {
                    window.scrollDir = 'down';
                } else {
                    window.scrollDir = 'up';
                }
                lastScrollPos = window.pageYOffset;
            })


            // Staggered in-transitions
            let elsToTrans = [];
            const startTrans = () => {
                for(const eli in elsToTrans) {
                    const el = elsToTrans[eli];
                    if(el === null) continue;

                    setTimeout(() => {
                        el.classList.add('--transition-in');
                        elsToTrans[eli] = null;
                        startTrans();
                    }, 20)

                    return true;
                }
            };
            window.transitionComponentIn = (el) => {
                console.log(el);
                elsToTrans.push(el);
                startTrans();
            };





