
import { mapGetters } from 'vuex'

import { TweenMax } from 'gsap';
import ScrollMagic from 'scrollmagic';
import 'animation.gsap'
import 'debug.addIndicators'

const mixin = {
    data() {
        return {
            suspendAnimations: true
        }
    },
    computed: {
        ...mapGetters(['scrollMagicController'])
    },
    mounted() {
        this.$store.subscribe((mutation, state) => {
            // Applies on all sections (including start)
            if (mutation.type === 'setScrollMagicController') {

                const transformer = this.$el.querySelector('.content-inner');
                if(!transformer) {
                    return;
                }
                const endEl = this.$refs.sectionBottom;


                window.addEventListener('scroll', () => {
                    const scrollMin = this.$el.offsetTop - window.innerHeight;
                    const scrollMax = this.$el.offsetTop + this.$el.clientHeight;

                    this.data.isInView = window.scrollY > scrollMin && window.scrollY < scrollMax;
                });




                new ScrollMagic.Scene({
                    triggerHook: 1,
                    //offset: offset,
                    triggerElement: endEl,
                    duration: window.innerHeight,
                })
                    .addIndicators()
                    .addTo(this.scrollMagicController)
                    .setTween(transformer, {
                        //opacity: 0,
                        //x: 200,
                        y: window.innerHeight/1.6 + 'px',
                        //scale: 1.5,
                        //ease: Linear.easeNone
                        ease: Power1.easeIn
                    })
                    .on('enter', () => {
                        this.suspendAnimations = true;
                    })
                    .on('leave', () => {
                        this.suspendAnimations = false;
                    })
            }


        });
    }
};

export default mixin;
